import { BpmnModel } from "../BpmnModel";
import { BpmnModelElement } from "../BpmnModelElement";
import { BpmnModelJsonHelper } from "../BpmnModelJsonHelper";
import { ExtensionMethod } from "../ExtensionMethod";
import { PalParticipant } from "../participant";
import { MexElement } from "./MexElement";

export class PalProcessAttribute extends MexElement {
    name: string;
    layer: number = 0;
    description: string;
    code: string;
    processType: string;
    input: string;
    output: string;
    departments: PalParticipant[] = [];
    responsiblePersons: PalParticipant[] = [];
    executiveDepartments: PalParticipant[] = [];
    securityClass: string;
    itSystemIds: string[] = [];
    preProcessIds: string[] = [];
    postProcessIds: string[] = [];

    constructor(model: BpmnModel) {
        super(model);
    }

    ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.name = null;
        this.layer = 0;
        this.description = null;
        this.code = null;
        this.processType = null;
        this.input = null;
        this.output = null;
        this.departments = [];
        this.responsiblePersons = [];
        this.executiveDepartments = [];
        this.securityClass = null;
        this.itSystemIds = [];
        this.preProcessIds = [];
        this.postProcessIds = [];
    }


    ConvertToJsonObject(): {} {
        const obj = super.ConvertToJsonObject();

        ExtensionMethod.SetAttributeValue(obj, "name", this.name);
        ExtensionMethod.SetAttributeValue(obj, "layer", this.layer);
        ExtensionMethod.SetAttributeValue(obj, "description", this.description);
        ExtensionMethod.SetAttributeValue(obj, "code", this.code);
        ExtensionMethod.SetAttributeValue(obj, "processType", this.processType);
        ExtensionMethod.SetAttributeValue(obj, "input", this.input);
        ExtensionMethod.SetAttributeValue(obj, "output", this.output);
        ExtensionMethod.SetAttributeValue(obj, "departments", this.departments ? this.departments.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "responsiblePersons", this.responsiblePersons ? this.responsiblePersons.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "executiveDepartments", this.executiveDepartments ? this.executiveDepartments.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "securityClass", this.securityClass);
        ExtensionMethod.SetAttributeValue(obj, "itSystemIds", BpmnModelJsonHelper.StringListToJson(this.itSystemIds));
        ExtensionMethod.SetAttributeValue(obj, "preProcessIds", BpmnModelJsonHelper.StringListToJson(this.preProcessIds));
        ExtensionMethod.SetAttributeValue(obj, "postProcessIds", BpmnModelJsonHelper.StringListToJson(this.postProcessIds));

        return obj;
    }

    LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.name = ExtensionMethod.GetAttributeValue4String(obj, "name");
        this.layer = ExtensionMethod.GetAttributeValue4Int64(obj, "layer");
        this.description = ExtensionMethod.GetAttributeValue4String(obj, "description");
        this.code = ExtensionMethod.GetAttributeValue4String(obj, "code");
        this.processType = ExtensionMethod.GetAttributeValue4String(obj, "processType");
        this.input = ExtensionMethod.GetAttributeValue4String(obj, "input");
        this.output = ExtensionMethod.GetAttributeValue4String(obj, "output");

        const departmentObjs = ExtensionMethod.GetAttributeValue(obj, "departments") as any[];
        this.departments = departmentObjs ? departmentObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        const responsiblePersonObjs = ExtensionMethod.GetAttributeValue(obj, "responsiblePersons") as any[];
        this.responsiblePersons = responsiblePersonObjs ? responsiblePersonObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        const executiveDepartmentObjs = ExtensionMethod.GetAttributeValue(obj, "executiveDepartments") as any[];
        this.executiveDepartments = executiveDepartmentObjs ? executiveDepartmentObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        this.securityClass = ExtensionMethod.GetAttributeValue4String(obj, "securityClass");
        this.itSystemIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "itSystemIds"));
        this.preProcessIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "preProcessIds"));
        this.postProcessIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "postProcessIds"));
    }

    clone(): PalProcessAttribute {
        const c = new PalProcessAttribute(this.Model);
        c.name = this.name;
        c.layer = this.layer;
        c.description = this.description;
        c.code = this.code;
        c.processType = this.processType;
        c.input = this.input;
        c.output = this.output;
        c.departments = this.departments.map(it => it.clone());
        c.responsiblePersons = this.responsiblePersons.map(it => it.clone());
        c.executiveDepartments = this.executiveDepartments.map(it => it.clone());
        c.securityClass = this.securityClass;
        c.itSystemIds = this.itSystemIds ? this.itSystemIds.map(it => it) : [];
        c.preProcessIds = this.preProcessIds ? this.preProcessIds.map(it => it) : [];
        c.postProcessIds = this.postProcessIds ? this.postProcessIds.map(it => it) : [];
        return c;
    }
}